body {
  margin: 0;
  padding: 0;
}

.container {
  min-width: 850px;
  margin: 0 auto;
  padding-left: 15px;
  padding-right: 15px;
}

.image {
  width: 100%;
  height: auto;
  border-top-left-radius: 12px;
  border-top-right-radius: 12px;
  flex-grow: 0;
  display: block;
}

p {
  margin: 0;
  padding: 4px 12px;
}

.search-form {
  text-align: center;
  background-color: #6495ed;
  padding-top: 10px;
  padding-bottom: 10px;
  position: relative;
}

.search-form input {
  border-radius: 5px;
  padding-top: 5px;
  padding-bottom: 5px;
  padding-left: 7px;
}

.btn {
  color: bisque;
  background-color: #4179e1;
  border-radius: 12px;
}

.btn-search {
  width: 90px;
  height: 30px;
}

.btn-load-more {
  width: 180px;
  height: 60px;
  margin: 0 auto;
  display: block;
}

.btn:disabled {
  background-color: gray;
}

a {
  color: #000;
  outline: none;
  text-decoration: none;
}

.gallery {
  min-width: 100%;
  flex-wrap: wrap;
  justify-content: center;
  gap: 24px;
  margin-top: 12px;
  margin-bottom: 12px;
  display: flex;
}

.movie-card {
  min-height: 100%;
  width: 400px;
  background-color: bisque;
  border: 1px solid #000;
  border-radius: 12px;
}

.info {
  padding-top: 4px;
  padding-bottom: 8px;
}

.hidden {
  display: none;
}

.count {
  text-align: center;
  background-color: #fff;
}

.load-more {
  margin-top: 60px;
  margin-bottom: 60px;
}

.btn:hover {
  color: #ff0;
  background-color: #00f;
}

.btn:hover:disabled {
  color: bisque;
  background-color: gray;
}

.sticky {
  position: sticky;
  top: 0;
}

.footer {
  height: 24px;
  background-color: #6495ed;
}

